const highlights = [
  {
    title: "High Quality",
    desc: "We provide only the best quality products.",
  },
  {
    title: "Affordable Price",
    desc: "Top products at the most affordable prices.",
  },
  {
    title: "Fast Delivery",
    desc: "Get your products delivered quickly and safely.",
  },
];

export default function ProductHighlights() {
  return (
    <section className="py-16">
      <h2 className="text-3xl font-bold text-center mb-10">Why Choose Us?</h2>
      <div className="grid md:grid-cols-3 gap-8">
        {highlights.map((item, index) => (
          <div
            key={index}
            className="p-6 bg-white rounded-lg shadow hover:shadow-lg text-center"
          >
            <h3 className="text-xl font-semibold mb-3">{item.title}</h3>
            <p className="text-gray-600">{item.desc}</p>
          </div>
        ))}
      </div>
    </section>
  );
}
